import React, { Component } from 'react'

import ScrollableAnchor from 'react-scrollable-anchor'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {
    faUser,
    faGraduationCap,
    faBriefcase,
    faPencilAlt,
} from '@fortawesome/free-solid-svg-icons'

class About extends Component {
    constructor(props) {
        super(props)

        this.state = {
            tab: [
                {
                    option: 0,
                    title: 'Who is Michael?',
                    icon: faUser,
                    content: [
                        "I live in Campinas, state of São Paulo. I'm a very curious person and I love study to understand how the things works.",
                        ' When I was 14 years old I started to learn about web development by myself. In my free time I like playing video game and go out with my family and friends.',
                    ],
                },
                {
                    option: 1,
                    title: 'College',
                    icon: faGraduationCap,
                    content: [
                        "I've a Bachelor’s degree in Information Systems at UNICAMP (University of Campinas is one of the best universities in Latin America).",
                        'In my last year I did a mobile 2D game using the framework Phaser, based in JavaScript.',
                        'Currently I’m doing a Specialization in Full Stack Web Development (Lato Sensu) where I’m learning more about Web main concepts that includes Node.js, NoSQL, JavaScript, etc (HTML5 APIs, Design Systems, Progressive Web Apps).',
                    ],
                },
                {
                    option: 2,
                    title: 'Work',
                    icon: faBriefcase,
                    content: [
                        'Over than 2 years experience with web development working with HTML, CSS, PHP, JavaScript and MySQL. ',
                        'Internship abroad in 2017 in Warsaw, Poland, using the Java programming language with a library called JasperReports to make reports in PDF and other formats.',
                        "Currently working as Front-end Developer at Venturus, where I'm developing modern web applications using React + Redux, ECMAScript 6+, CSS preprocessors (such as Sass and Less) and Styled-components, API (JSON, axios, Fetch API), HTML5 and Jest + Enzyme.",
                    ],
                },
                {
                    option: 3,
                    title: 'Training',
                    icon: faPencilAlt,
                    content: [
                        'Front-End Web Development with React - The Hong Kong University of Science and Technology - 4 weeks - April of 2019',
                        'React Router and Redux - Microsoft - edX - February of 2019 - 2 weeks',
                        'Introduction to ReactJS - Microsoft - edX - February of 2019 - 2 weeks',
                        'Angular para construção de Web Apps (“Angular to build Web Apps”) - Caelum - March of 2018 - 40 hours',
                        'JavaScript Moderno e os fundamentos para construção de Web Apps ("Modern JavaScript to build Web Apps") - Caelum - April of 2018 - 30 hours ',
                    ],
                },
            ],
            currentText: '',
        }
    }

    tabListOption = tabcontent => {
        return (
            <ul>
                {tabcontent.map(t => (
                    <li
                        key={t.option}
                        onClick={() => this.showContentTab(t.content)}
                    >
                        <FontAwesomeIcon icon={t.icon} /> {t.title}
                    </li>
                ))}
            </ul>
        )
    }

    showContentTab = content => {
        this.setState({ currentText: content })
    }

    render() {
        return (
            <ScrollableAnchor id={'about-me'}>
                <section className="about-me">
                    <div className="content">
                        <div className="letter">
                            {this.state.currentText.length === 0 ? (
                                <h2>M</h2>
                            ) : (
                                this.state.currentText.map((p, i) => (
                                    <p key={i}>{p}</p>
                                ))
                            )}
                        </div>
                        <div className="col-1">
                            {this.tabListOption(this.state.tab)}
                        </div>
                    </div>
                </section>
            </ScrollableAnchor>
        )
    }
}

export default About
