import React from 'react';
import ScrollableAnchor from 'react-scrollable-anchor';

const Intro = () => {
    return(
        <ScrollableAnchor id={'home'}>
        <section className="intro">
            <div className="content">
            <div className="col-2">
                <h1>Front-end developer</h1>
                <p>Hello! My name is Michael, I'm brazilian and I'm 25 years old. I define myself as a developer that always want to understand the entire development process and bring the best experience for the user.</p>
                </div>
            <div className="col-2">
                <img alt="Michael Almeida" className="profile" src="https://michaelalmeida.github.io/static/media/profile.0cce7113.jpg" />
            </div>
            </div>
        </section>
        </ScrollableAnchor>
    )
}

export default Intro;