import React, { Component } from 'react';
import firebase from '../firebase/config.js';
import ScrollableAnchor from 'react-scrollable-anchor';

class Projects extends Component {
    constructor(props) {
        super(props);

        this.state = {
            projects: [],
            projectSeletected: null,
            displayProjectsList: 'block'
        }
    }

    componentDidMount() {
        var db = firebase.firestore();
        const projects = [];

        db.collection("projects").get().then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
                const { name, subtitle, description, image, url, tags } = doc.data();
                const id = doc.id;
                projects.push(
                    {
                        id,
                        name,
                        subtitle,
                        description,
                        image,
                        url,
                        tags
                    }
                )

            });
            this.setState({ projects: projects })
        });
    }

    renderProject = (projects) => {
        return (
            <ul className="project-list">
                {projects.map(
                    p =>
                        <li key={p.id} onClick={() => this.selectProject(p)}>
                            <img src={p.image} alt={p.name} />
                            <span>
                                <h3>{p.name}</h3>
                                <h4>{p.subtitle}</h4>
                            </span>
                        </li>
                )}
            </ul>
        )
    }

    selectProject = (selected) => {
        this.setState({ projectSeletected : selected,  displayProjectsList: 'none'})
    }

    lightBox = (projectSeletected) => {
        return (
            <div className="lightbox">
                <span onClick={() => this.closeLightBox()}>x</span>
                <div className='col-1'>
                    <img src={projectSeletected.image} alt={projectSeletected.name} />
                    <ul>{projectSeletected.tags.map((t, i) => <li key={i}>{t}</li>)}</ul>
                </div>
                <div className='col-2'>
                    <h3>{projectSeletected.name}</h3>
                    <h4>{projectSeletected.subtitle}</h4>
                    {projectSeletected.description.map((d, i) => <p key={i}>{d}</p>)}
                    <a href={projectSeletected.url} rel="noopener noreferrer" target="_blank">{projectSeletected.url}</a>
                </div>
            </div>
        )
    }

    closeLightBox = () => {
        this.setState({ projectSeletected: null, displayProjectsList: 'block'})
    }

    render() {
        return (
            <ScrollableAnchor id={'projects'}>
            <section className="projects">
                <div className="content">
                    <div style={{display: this.state.displayProjectsList}}>
                        {this.state.projects.length === 0 ? 'Loading...' : this.renderProject(this.state.projects)}
                    </div>
                    {this.state.projectSeletected !== null ? this.lightBox(this.state.projectSeletected) : ''}
                </div>
            </section>
            </ScrollableAnchor>
        )
    }
}

export default Projects;