import React, { Component } from 'react'
import ScrollableAnchor from 'react-scrollable-anchor'

import { CV } from './CV'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faEnvelope } from '@fortawesome/free-solid-svg-icons'
import {
    faSkype,
    faGithub,
    faLinkedinIn,
    faBehance,
} from '@fortawesome/free-brands-svg-icons'

class Contact extends Component {
    constructor(props) {
        super(props)

        this.state = {
            contactList: [
                {
                    icon: faEnvelope,
                    type: 'email',
                    content: 'michaelralmeida@gmail.com',
                },
                {
                    icon: faSkype,
                    type: 'skype',
                    content: 'michaelralmeida',
                },
                {
                    icon: faGithub,
                    type: 'github',
                    content: 'github.com/michaelalmeida',
                },
                {
                    icon: faLinkedinIn,
                    type: 'linkedin',
                    content: 'linkedin.com/in/michaelralmeida',
                },
                {
                    icon: faBehance,
                    type: 'behance',
                    content: 'behance.net/maycoualmec67b',
                },
            ],
        }
    }
    render() {
        return (
            <ScrollableAnchor id={'contact'}>
                <section className="contact">
                    <div className="content">
                        <div className="letter">
                            <ul>
                                {this.state.contactList.map(l => (
                                    <li key={l.type}>
                                        <FontAwesomeIcon icon={l.icon} />{' '}
                                        {l.content}
                                    </li>
                                ))}
                            </ul>
                        </div>
                        <div className="col-1">
                            <CV />
                        </div>
                    </div>
                </section>
            </ScrollableAnchor>
        )
    }
}

export default Contact
