import React from 'react'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faReact } from '@fortawesome/free-brands-svg-icons'
import { faCode, faRulerCombined } from '@fortawesome/free-solid-svg-icons'

const Skills = () => {
    return (
        <section className="skills">
            <div className="content">
                <div className="box">
                    <div className="col-3">
                        <FontAwesomeIcon icon={faReact} size="3x" />
                        <h2>React+Redux</h2>
                        <p>
                            I've been working and studying React with Redux and
                            others tools that belongs to the this universe.
                        </p>
                    </div>
                    <div className="col-3">
                        <FontAwesomeIcon icon={faCode} size="3x" />
                        <h2>JavaScript</h2>
                        <p>
                            I've been using the power of JavaScript to build Web
                            Applications since the Backend till Frontend
                        </p>
                    </div>
                    <div className="col-3">
                        <FontAwesomeIcon icon={faRulerCombined} size="3x" />
                        <h2>UI</h2>
                        <p>
                            I always care about the application interface. The
                            user has to feel comfortable using the software.{' '}
                        </p>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default Skills
